.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;

  padding: 10px 24px;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.87;
  letter-spacing: 0.06em;

  border: none;
  color: $whiteColor;
  background-color: $accentColor;
  border-radius: 4px;
  cursor: pointer;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: $accentColorHover;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  }
}

.menu__btn {
  width: 40px;
  height: 40px;

  display: flex;
  justify-content: center;
  align-items: center;

  margin-left: auto;

  padding: 0;

  color: $secondaryColor;

  background-color: transparent;
  border: none;

  transition: color 250ms;
  &:hover,
  &:focus {
    background-color: #188ce8;
  }

  @media screen and (min-width: 768px) {
    display: none;
  }

  &:active {
    color: $accentColor;
  }
}

.menu__btn--icon {
  fill: currentColor;
  display: flex;
}

// Mobile Menu Styles
.menu-container {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;

  padding: 48px 40px;

  background: $whiteColor;
  box-shadow:
    0px 4px 8px rgba(0, 0, 0, 0.04),
    0px 2px 4px rgba(0, 0, 0, 0.08),
    0px 1px 3px rgba(0, 0, 0, 0.16);

  transform: translateX(100%);
  transition: transform 400ms;

  &.is-open {
    transform: translateX(0);
  }
}

.menu-toggle {
  position: absolute;

  top: 10px;
  right: 15px;
}

.mobile__nav {
  margin-bottom: auto;

  & li {
    margin-bottom: 32px;
  }

  & a {
    font-weight: 500;
    font-size: 40px;
    line-height: 1.17;
    letter-spacing: 0.02em;

    margin-bottom: 32px;

    color: $secondaryColor;

    &:active {
      color: $accentColor;
    }
  }
}

.mobile__contacts {
  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  letter-spacing: 0.02em;

  margin-bottom: 64px;
}

.mobile__contacts--mail {
  color: $mainColor;
}

.mobile__contacts--phone {
  display: block;

  margin-bottom: 32px;

  color: $accentColor;

  @media screen and (min-width: 425px) {
    font-size: 40px;
  }
}

.mobile__socials {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;

  & a {
    font-weight: 500;
    font-size: 18px;
    line-height: 1.22;
    letter-spacing: 0.02em;

    color: var(--accent-color);
  }
}

.mobile__socials--pos {
  position: relative;
}

.mobile__socials--decor::after {
  content: "";

  position: absolute;
  top: 0;
  left: calc(100% + 10px);

  display: block;

  width: 1px;
  height: 22px;

  border: 1px solid rgba(33, 33, 33, 0.2);
}
